.contestants-wrapper{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /* justify-content: center; */
    -ms-flex-direction: column;
        flex-direction: column;
    padding-right: 2rem;
    padding-left: 2rem;
    /* margin-bottom: 0px; */
    height: 100vh;
    background-color: black;
}

.contestants-wrapper .content{
   /* padding-top: 200px; */
   height: 100vh;
   -ms-flex-wrap: wrap;
       flex-wrap: wrap;
   -ms-flex-direction: column;
       flex-direction: column;
   margin-top: 10rem;
}

.contestants-wrapper .single{
    /* padding-top: 200px; */
    /* height: 100vh; */
    /* flex-wrap: wrap; */
    -ms-flex-direction: row;
        flex-direction: row;
        display: -ms-flexbox;
        display: flex;
    /* margin-top: 10rem; */
 }

.single .list{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row; 
    /* flex-wrap: wrap; */
    width: 50%;
    /* align-items: center; */
    /* height: 100vh; */
    overflow-y: scroll;
    padding: 4rem;
    margin-top: 15rem;
    padding-left: 0px;
}

.single .view{
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 4rem;
    margin-top: 15rem;
    margin-left: 0px;
    height: 100%;
    overflow-y: scroll;
}

.bio p{
    margin: 0px;
}

.image-wrapper{
    width: 75px;
    height: 75px;
    min-width: 75px;
    margin-right: 4rem; 
    border-radius: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    overflow: hidden;
    background-color:black;
    border: 1px solid rgba(255,255,255,1);
}

.image-wrapper img{
    height: 100%;
    /* width: 100%; */
}

.ordered{
    /* width: 100%; */
    padding-right: 2rem;
    max-width: 400px;
    padding: 4rem;
}

.ordered li {
    margin-bottom: 1.5rem;
}

.ordered li a h3, h1{
    font-size: 28px;
    line-height: 1.35;
}

.letter{
    width: 32px;
    height: 32px;
    border: 1px solid rgba(255,255,255,1);
    text-align: center;
    font-size: 20px;
    margin-bottom: 2rem;
    line-height: 1.8;
    /* padding-bottom: rem; */
}

.single .link{
    text-align: center;
}


